    .banner-container {
        position: relative;
        height: 100%;
        overflow: hidden;
    }
    
    .banner-text {
        background-color: rgb(0, 0, 0, 50%);
        text-shadow: 0.5rem 0.5rem rgb(134, 130, 130);
        opacity: 0;
        padding: 2rem 2rem;
        font-size: 4rem;
        color: blanchedalmond;
        position: absolute;
        top: 50%;
        left: 50%;
        -webkit-transform: translate(-50%, -50%);
                transform: translate(-50%, -50%);
        -webkit-animation: showUp 1.5s 1s;
                animation: showUp 1.5s 1s;
        -webkit-animation-fill-mode: forwards;
                animation-fill-mode: forwards;
        z-index: 10;
    }
    
    @-webkit-keyframes showUp {
        from {
            opacity: 0;
        }
        to {
            opacity: 1;
        }
    }
    
    @keyframes showUp {
        from {
            opacity: 0;
        }
        to {
            opacity: 1;
        }
    }
    
    @-webkit-keyframes enlarge {
        from {
            -webkit-transform: scale(1, 1);
                    transform: scale(1, 1)
        }
        to {
            -webkit-transform: scale(1.15, 1.15);
                    transform: scale(1.15, 1.15);
        }
    }
    
    @keyframes enlarge {
        from {
            -webkit-transform: scale(1, 1);
                    transform: scale(1, 1)
        }
        to {
            -webkit-transform: scale(1.15, 1.15);
                    transform: scale(1.15, 1.15);
        }
    }
    
    .banner-image {
        -webkit-animation: enlarge 6s 2s forwards;
                animation: enlarge 6s 2s forwards;
        -webkit-transform: scale(1, 1);
                transform: scale(1, 1);
        width: 100%;
        max-height: 100vh;
        object-fit: cover;
